import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { forkJoin } from 'rxjs';
import { RoleModel } from 'src/app/shared/models/model';
import { InstitutionService } from 'src/app/shared/services/institution.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html'
})
export class UserComponent implements OnInit {
  userInfo: any;
  userForm!: FormGroup;
  users: any[] = [];
  display: boolean = false;
  roles: RoleModel[] = [];
  institutions: any[] = [];

  constructor(private fb: FormBuilder, private roleService: RoleService, private institutionService: InstitutionService, private userService: UserService,
    private route: ActivatedRoute, private toastrService: ToastrService) {

    this.userForm = this.fb.group({
      id: [''],
      userName: ['', Validators.required],
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      phoneNumber: ['', Validators.required],
      email: ['', Validators.required],
      roleId: ['', Validators.required],
      userInstitutionDtos: [],
    });

    this.route.pathFromRoot[1].data.subscribe(r => {
      this.userInfo = r['user'];
    });
  }

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    forkJoin([this.roleService.getAll(), this.userService.getAll(this.userService.selectedInstitutionId), this.institutionService.get()]).subscribe(([roles, users, institutions]) => {
      this.roles = roles as any[];
      this.users = users as any[];
      const institutionListTemp = institutions as any[];
      const userRole = this.userInfo?.role?.name.replace(/\s+/g, '').toLowerCase();

      if (['superadmin'].includes(userRole)) {
        this.institutions = !institutionListTemp?.length ? [] : institutionListTemp.map((ins: any) => {
          return {
            institutionId: ins.id,
            institutionName: ins.name
          };
        });
      } else {
        this.institutions = this.userInfo?.userInstitutionDtos ?? [];
      }
    });
  }

  showDialog() {
    this.display = true;
    this.userForm.reset();
  }

  updateData() {
    let temp = [...this.users];
    temp = JSON.parse(JSON.stringify(temp));
    this.users = temp;
  }

  add() {
    const data = this.userForm.value;
    data['phoneNumber'] = data['phoneNumber'].toString();

    if (!data.id) {
      delete data.id;
    }

    this.userService.create(data).subscribe({
      next: (response: any) => {
        if (!!response.id) {
          this.users.push(response);
          this.updateData();
          this.toastrService.success('User create successfully.');
          this.display = false;
        }
      },
      error: () => {
        this.toastrService.error('User create failed.');
      }
    });
  }

  edit() {
    const data = this.userForm.value;
    data['phoneNumber'] = data['phoneNumber'].toString();

    this.userService.update(data).subscribe((response: any) => {
      if (!!response.id) {
        const data = response;
        const index = this.users.findIndex(i => i.id === data.id);
        this.users[index] = data;
        this.toastrService.success('User updated successfully.');
        this.display = false;
      }
    });
  }

  onSubmit() {
    if (this.userForm.invalid) {
      return;
    }

    if (!this.userForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(user: any) {
    this.userService.getById(user.id).subscribe((response : any) => {
      if(response?.userInstitutionDtos?.length > 0) {
        response.userInstitutionDtos = response.userInstitutionDtos.map((ui : any) => {
          return {
            institutionId: ui.institutionId,
            institutionName: ui.institutionName            
          };
        });
      }

      this.userForm.patchValue(response);
      this.display = true;
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }
}
